/* COMPONENTS */
/* LAYOUT */
.container {
  width: 100%;
  height: 100vh;
  padding-top: 15rem;
  position: relative; }
  .container::after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 0; }
  .container video {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: -1; }

.prueba {
  height: 150vh; }

.header {
  width: 100%;
  height: 100%;
  transition: height 1s;
  position: fixed;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10; }
  .header__body {
    width: 90%;
    max-width: 70rem;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative; }
    @media only screen and (min-width: 991px) {
      .header__body {
        align-items: flex-start; } }
  .header.big .containerLogo {
    margin-top: calc(100vh - 70vh);
    align-items: center; }
    @media only screen and (min-width: 991px) {
      .header.big .containerLogo {
        align-items: center; } }
    .header.big .containerLogo .logo {
      width: 150px;
      height: 150px; }
      @media only screen and (min-width: 991px) {
        .header.big .containerLogo .logo {
          width: 250px;
          height: 250px; } }
    .header.big .containerLogo .title {
      opacity: 1;
      font-size: 3rem; }
      @media only screen and (min-width: 991px) {
        .header.big .containerLogo .title {
          font-size: 5rem; } }
  .header.big .containerHamburguer {
    position: absolute;
    top: 0;
    right: 0; }

.navigation {
  justify-content: center;
  align-items: center; }
  .navigation .menu {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 4rem;
    padding: 0; }
    .navigation .menu:first-of-type {
      margin-right: 0; }
      @media only screen and (min-width: 991px) {
        .navigation .menu:first-of-type {
          margin-right: 1.5rem; } }
    .navigation .menu li a {
      text-transform: uppercase;
      color: #FFF;
      font-weight: bold;
      letter-spacing: 0.3rem; }
      .navigation .menu li a:hover {
        color: #F2D400; }
    .navigation .menu li.current-menu-item {
      position: relative; }
      .navigation .menu li.current-menu-item::after {
        content: '';
        display: block;
        width: 80%;
        height: 2px;
        background-color: #F2D400;
        position: absolute;
        bottom: -10px;
        left: 50%;
        transform: translateX(-50%); }
  .navigation.desktop {
    display: none; }
    @media only screen and (min-width: 991px) {
      .navigation.desktop {
        display: flex; } }
  .navigation.mobile {
    opacity: 0;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    backdrop-filter: blur(5px);
    transition: opacity .5s linear;
    background-color: rgba(0, 0, 0, 0.5); }
    @media only screen and (min-width: 991px) {
      .navigation.mobile {
        display: none; } }
    .navigation.mobile ul {
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
      .navigation.mobile ul li a {
        text-shadow: 0px 0px 10px #F2D400; }

